<template>
  <div>

  <div class="container">

    <h1>Velkommen til <span class="logo-span">LPM</span>anager!</h1>

      <div v-if="!classImported">
        <p>Kom i gang ved  importere klassen du er lrer for i CSV-format</p>

        <label class="import-btn" v-if="showButton">
          <input type="file" accept=".csv" @change="loadCSV($event)">
          Importer klasse
        </label>
        
        <p class="errorMsg" v-if="errorMsg">Noe gikk galt, prv  laste inn siden og importere p nytt</p>
        <app-loading-screen v-if="isLoading"></app-loading-screen>

        <p v-if="errorMsg"><span>Merk:</span> Det er viktig at klassen blir importert som en korrekt csv-fil.
        Det vil si en csv-fil p dette formatet: *****
        administrasjonen skal kunne utlevere en slik fil fra extens / visma
        </p>
      </div>

      <div v-if="classImported">
        <p id="top-para">Klikk p <span class="bold">EKSAMEN</span> om du skal forberede en eksamen, eller</p>
        <p id="bot-para">klikk p <span class="bold">YFF</span> om du skal forberede til Yrkesfaglig Fordybning</p>
        <router-link to="/Eksamen" tag="button" class="link-btn">Eksamen</router-link>
        <router-link to="/YFF" tag="button" class="link-btn">YFF</router-link>
      </div>
  </div>

  <div class="browser-melding" v-if="!brukbarBrowser">
      <p class="bm-p-top">Venligst bruk en respektabel nettleser, som Chrome eller Firefox.</p>
      <p class="bm-p-bot">Internet Explorer har mange feil og mangler, og stttes ikke fullstending av LPManager.</p>
  </div>

  </div>
</template>

<script>
import Papa from 'papaparse'
import LoadingScreen from '../components/LoadingScreen'
import { detect } from 'detect-browser'

export default {
  data () {
    return {
      isLoading: false,
      showButton: true,
      errorMsg:false,
      brukbarBrowser:true
    }
  },
  methods: {
    loadCSV (event) {
      var file = event.target.files[0]

      if (event.target.files.length < 1) {
        this.errorMsg = true
      } else {
        this.startLoading()
      }

      Papa.parse(file, {
        header: true,
        dynamicTyping: true,
        complete: (results) => {
          for(let i = 0; i < results.data.length; i++){
            this.$store.commit('addElev', results.data[i])
          }
        }
      })
    },
    startLoading(){
      this.showButton = false
      this.isLoading = true

      setTimeout(() => {
        this.isLoading = false
      },1500)
    }
  },
  computed:{
    classImported () {
      if (this.$store.getters.get_klasse.length > 0 && this.isLoading === false) {
        return true
      } else {
        return false
      }
    }
  },
  components: {
    appLoadingScreen:LoadingScreen
  },
  created(){
    const browser = detect()

    switch(browser && browser.name) {
      case 'chrome':
      case 'firefox':
        this.brukbarBrowser = true
        break
      default:
        this.brukbarBrowser = false
    }
  }
}
</script>

<style scoped>
  .browser-melding {
    font-family: 'Ubuntu', sans-serif;
    text-align: center;
    width: 50%;
    padding:20px;
    padding-top:6px;
    padding-bottom:6px;
    margin-top:30px;
    border-radius:5px;
    margin-left: auto;
    margin-right: auto;
    font-size: 0.8rem;
    background-color: #E37222;
    -webkit-box-shadow: 0px 0px 6px -1px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 6px -1px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 6px -1px rgba(0, 0, 0, 0.75);
  }

  .bm-p-top{
    padding:0;
    margin:5px 0;
  }

  .bm-p-bot{
    padding:0;
    margin:5px 0;
  }

  .container {
    font-family: 'Ubuntu', sans-serif;
    text-align: center;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 70px;
    padding: 70px;
    background-color: #078893;
    color: white;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 6px -1px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 6px -1px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 6px -1px rgba(0, 0, 0, 0.75);
  }

  .link-btn {
    width: 100px;
    height: 40px;
    margin: 10px;
    border-radius: 5px;
    border: 1px solid darkslategrey;
    font-weight: bold;
    color: white;
    background-color: #E37222;
  }

  .link-btn:hover {
    background-color: #e88640;
    color: black;
  }

  .link-btn:active {
    background-color: #a85f2b;
    color: white;
  }

  .bold {
    font-weight: bold;
  }

  .logo-span {
    color: #282827;
    font-weight: bold;
  }

  #top-para {
    margin-top: 50px;
  }

  #bot-para {
    margin-bottom: 50px;
  }

  .import-btn {
    border: 1px solid darkslategrey;
    display: inline-block;
    width: 115px;
    height: 20px;
    background-color: #E37222;
    border-radius: 5px;
    padding: 6px 12px;
    color: white;
  }

  .import-btn:hover {
    background-color: #e88640;
    color: black;
  }

  .import-btn:active {
    background-color: #a85f2b;
    color: white;
  }

  input[type="file"] {
    display: none;
  }

  .errorMsg{
    color:red;
    background-color: antiquewhite;
    border-radius:5px;
    padding:2px;
  }
</style>
